.footer-wrap {
  position: relative;
  z-index: 1;
  padding: 34px 0 29px;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__cr {
    padding-left: 36px;
    opacity: 0.45;
    font-family: $font-family;
    font-size: 13px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #3c3d41;
  }

  &__nav {

  }

  &__social {

  }

}

.footer-menu {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    margin-left: 46px;

    &:last-child {
      margin-left: 65px;
    }

  }

  &__link {
    font-family: $font-family;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
    text-transform: uppercase;
    text-decoration: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: #f5a623;
    }

  }

}

