.btn {
  display: inline-block;
  padding: 14px 30px 16px;

  font-family: $font-family;
  font-size: 18px;
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.8px;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  border-radius: 50px;
  border: none;
  outline: none;
  background-image: linear-gradient(79deg, #d20b54, #ffb84a);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  transition: box-shadow .2s ease-in-out;
  white-space: nowrap;

  &:hover {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  }

  &_wt {
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(84deg, #d20b54, #ffb84a);
    color: #fff;
  }

}

.btn-wt-wrap {
  display: inline-block;
  border-radius: 50px;
  background: #ffffff;
}

.btn-group {
  display: flex;
  margin: 45px 0 0 0!important;
  padding: 0!important;
  list-style: none;

  & > li {
    margin-right: 35px;

    &:last-child {
      margin-right: 0;
    }

  }
}
