.rating-line {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(15deg, #d20b54, #ffb84a);
  -webkit-clip-path: polygon(100% 0, 0 0, 100% 100%);
  clip-path: polygon(100% 0, 0 0, 100% 100%);
  overflow: hidden;

  &:after {
    content: '';
    position: absolute;
    top: 50px;
    right: 0;
    background: url("../images/rating-line.svg") no-repeat;
    background-size: 40px 100%;
    width: 40px;
    height: 100%;
    opacity: .5;
  }

  &_alt {

    &:after {
      top: 20px;
    }

  }

}

.rating-line-wrap {
  flex: 0 0 194px;
  padding-bottom: 45px;
  //height: 100%;
  background-color: rgba(204, 204, 204, 0.1);

  &_w-bg {
    flex: 0 0 125px;
    background-color: transparent;
  }

}

.rating-detail {
  display: flex;
  flex: 1 1 274px;
  padding-top: 47px;

  &__label-box {
    flex: 1 1 77px;
  }

  &__label-group {

    & ul {
      margin: 0;
      padding: 50px 0 97px 16px;
      list-style: none;

      & li {
        margin-bottom: 40px;
        font-family: $font-family;
        font-size: 18px;
        font-weight: 900;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #000000;

        &:last-child {
          margin-bottom: 0;
        }

      }

    }

  }

  &__title-box {
    flex-grow: 1;
    padding-left: 20px;
  }

  &__title-group {
    padding-bottom: 50px;

    & .y1 {
      color: #f9c856;
    }

    & .y2 {
      color: #f9a842;
    }

    & .y3 {
      color: #f77d27;
    }

    & h3 {
      margin: 0;
      font-family: $font-family;
      font-size: 36px;
      font-weight: 400;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 5px;
      text-align: left;
      color: #f9c856;
      text-transform: uppercase;
    }

    & ul {
      margin: 0;
      padding: 5px 0 0 0;
      list-style: none;

      & li {
        margin-bottom: 24px;
        font-family: $font-family;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.21;
        letter-spacing: normal;
        text-align: left;
        color: #000000;

        &:last-child {
          margin-bottom: 0;
        }

      }

    }

  }

}

.rating {
  display: flex;
  padding-top: 35px;
}

.rating-label {
  padding: 20px 20px 20px 0;

  &__item {
    font-family: $font-family;
    font-size: 24px;
    font-weight: 900;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #ffb54a;
    padding-bottom: 60px;

    &.y1 {
      color: #ffb54a;
    }

    &.y2 {
      color: #f89d4c;
    }

    &.y3 {
      color: #f1814d;
    }

    &.y4 {
      color: #e9664e;
    }

    &.y5 {
      color: #e24b50;
    }

    &.y6 {
      color: #db2f52;
    }

  }

}

.rating-list-wrap {
  flex: 1 1 auto;
  padding-left: 50px;
}

.rating-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
  //margin: 0 -18px;

  &__item {
    width: 33.33%;
    //padding: 0 18px 36px;
  }

}

.rating-list-profile {
  display: block;
  padding: 20px;
  font-family: $font-family;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #ffb54a;
  text-decoration: none;
  transition: all .2s ease-in-out;

  &.y1 {
    color: #ffb54a;
  }

  &.y2 {
    color: #f89d4c;
  }

  &.y3 {
    color: #f1814d;
  }

  &.y4 {
    color: #e9664e;
  }

  &.y5 {
    color: #e24b50;
  }

  &.y6 {
    color: #db2f52;
  }

  & span {
    display: block;
  }

  & span:first-child {
    font-weight: 700;
  }

  &:hover {
    border-radius: 8px;
    background-color: #ffffff;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.13);
  }


}
