.calendar-list {
  display: flex;
  flex-wrap: wrap;
  padding-top: 70px;
  margin: 0 -18px;

  &__item {
    width: 33.33%;
    padding: 0 18px 36px;
  }

}

.calendar {
  padding: 25px;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.13);

  &__head {

    & > h2 {
      margin: 0;
      font-family: $font-family;
      font-size: 18px;
      font-weight: bold;
      line-height: 1.17;
    }

    & > p {
      margin-top: 3px;
      font-family: $font-family;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #f5a623;
    }

  }

  & > p {
    font-family: $font-family;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: #000000;
  }

}
