.content-wrap {
  position: relative;
  width: 100%;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  //-webkit-box-orient: vertical;
  //-ms-flex-flow: column nowrap;
  flex-flow: column nowrap;
  height: 100%;
  min-height: 100vh;
}

.content {
  padding: 50px 0 50px 35px;

  &_mw {
    max-width: 80%;
  }

  &_fs-lg {

    & > p {
      font-size: 18px!important;
    }

    & > ul {
      font-size: 18px!important;
      ine-height: 1.56;
    }

  }

  & > h1 {
    margin: 0;
    font-family: $font-family;
    font-size: 48px;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.08;
    letter-spacing: 2px;
    text-align: left;
    text-transform: uppercase;
    color: #000000;
  }

  & > h2 {
    margin: 0;
    font-family: $font-family;
    font-size: 36px;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: 3.6px;
    text-align: left;
    color: #000000;
    text-transform: uppercase;
  }

  & > h3 {
    font-weight: 700;
    font-size: 18px;
    color: #000000;
    line-height: 1.69;
    text-transform: uppercase;
  }

  & > p {
    margin: 45px 0 0 0;
    font-family: $font-family;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.69;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
  }

  & > ul {
    margin: 0;
    padding-left: 20px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.69;
  }

}
