.partner {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 15px 0 0 35px;
  list-style: none;

  &__item {
    margin-right: 55px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__item-link {
    display: inline-block;
  }

  &__item-img {
    vertical-align: top;

    &_1 {
      width: 67px;
      height: 76px;
    }

    &_2 {
      width: 134px;
      height: 51px;
    }

    &_3 {
      width: 153px;
      height: 20px;
    }

    &_4 {
      width: 120px;
      height: 41px;
    }

  }

}
